.zolo-pages-preview-container {
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    height: 100%;
    background: #edf0f4;
    padding: 30px;
    z-index: 9;

    .zolo-demos-wrapper {
        padding: 0 !important;
        // display: grid;
        // grid-template-columns: repeat(1, 1fr);
        grid-gap: 10px;
        // columns: 2 !important;
        .footer-left {
            width: 100%;
            box-sizing: border-box;
        }
    }

    .header {
        background-color: #fff;
        padding: 20px;
        border-radius: 6px;
        display: flex;
        align-items: center;
        gap: 10px;
        margin-bottom: 20px;
        h2 {
            margin: 0;
            font-size: 20px;
        }
        img {
            max-width: 100%;
        }
        .close-btn {
            display: flex;
            align-items: center;
            gap: 5px;
            border: 0;
            padding: 8px 10px;
            font-size: 12px;
            border-radius: 6px;
            font-weight: 600;
            color: #000;
            background: #f0f0f0;
            cursor: pointer;
            transition: 0.3s ease;
            svg {
                width: 18px;
                height: 18px;
                display: block;
            }
            &:hover {
                background: #e0e0e0;
            }
        }
    }

    .zolo-demos-wrapper {
        column-count: 1 !important;
    }

    .pages-list {
        display: flex;
        align-items: stretch;
        gap: 20px;
    }

    .all-pages {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 15px;
    }

    .single-page {
        width: 100%;
        height: 400px;
        box-sizing: border-box;
        position: relative;
        border: 4px solid #ffffff;
        padding: 4px;
        background: #fff;
    }

    .single-page img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .single-page.active {
        border-color: #2567ff;
    }

    .pages-list-and-action {
        max-height: 100vh;
        overflow-x: hidden;
        overflow-y: scroll;
    }
}

.zolo-pages-templates {
    .single-demo {
        cursor: pointer;
        .demo-footer {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }

    .photos-group {
        .single-page-photo {
            display: none;
            &:first-child {
                display: block;
            }
        }
    }
}

@media (min-width: 768px) {
    .zolo-pages-preview-container {
        .zolo-demos-wrapper {
            columns: 2 !important;
        }
    }
}
@media (min-width: 1440px) {
    .zolo-pages-preview-container {
        .zolo-demos-wrapper {
            columns: 3 !important;
        }
    }
}
